@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}

header {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.07), 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  color: #212529;
}

.dropdown {
  display: none;
}

.dropdown.show {
  display: block;
}

.nav-area {
  display: flex;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 10px 20px;
}

.menu-items {
  position: relative;
  font-size: 16px;
}

.menu-items a {
  display: block;
  font-size: inherit;
  color: inherit;
  text-decoration: none;
}

.menu-items button {
  display: flex;
  align-items: center;
  color: inherit;
  font-size: inherit;
  border: none;
  background-color: transparent;
  cursor: pointer;
  width: 100%;
}

button span {
  margin-left: 3px;
}

.menu-items > a, .menu-items button {
  text-align: left;
  padding: 0.7rem 1rem;
}

.menu-items a:hover,
.menu-items button:hover {
  background-color: #f2f2f2;
}

/* content */

.content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 3rem 20px;
}

.content h2 {
  margin-bottom: 1rem;
}

.content a {
  color: #cc3852;
  margin-right: 10px;
}

.blockcontainer {
  position: relative;
  text-align: center;
  height: 400px;
  background-size: cover;
  background-position: center;
}

.blockheading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.796);
  padding: 15px;
  border-radius: 5px;
  font-size: 4vw;
}

.blockinfo {
  background-color: rgb(41 38 38 / 86%); 
  color: #cbcbcb;
  position: absolute;
  bottom: 8px;
  right: 16px;
  font-size: 3vw;
  padding: 10px;
  box-sizing: border-box;
}

.blocklink {
  color:#4295e5
}

.blocklink:hover {
  color: #106fca;
}

.blockwrapper {
  max-width: 95vw;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .menus {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    list-style: none;
  }

  .dropdown {
    position: absolute;
    left: 0;
    left: auto;
    box-shadow: 0 10px 15px -3px rgba(46, 41, 51, 0.08),
      0 4px 6px -2px rgba(71, 63, 79, 0.16);
    font-size: 0.875rem;
    z-index: 9999;
    min-width: 10rem;
    padding: 0.5rem 0;
    list-style: none;
    background-color: #fff;
    border-radius: 0.5rem;
  }

  .dropdown .dropdown-submenu {
    position: absolute;
    left: 100%;
    top: -7px;
  }

  .blockbox {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 5px;
    row-gap: 5px;
  }

  .blockpadding {
    padding-bottom: 5px;
    padding-top: 5px;
  }

  .blockinfo {
    font-size: 1.5vw;
  }

  .blockheading {
    font-size: 2vw;
  }

}

@media screen and (max-width: 768px) {
  .dropdown {
    padding-left: 10px;
  }

  .menu-items {
    font-size: 1.5rem;
  }

  .blockcontainer {
    margin-top: 10px;
    height: 250px;
  }

}